<template>
  <div id="world-map">
    <div class="map-wrapper">
      <h2
        v-if="country"
        class="country-title"
      >
        {{ country.name }}
      </h2>
      <div
        v-if="currentCountry"
        class="country-info"
      >
        <h3 class="text-center">
          {{ currentCountry.state }}
        </h3>
        <ul>
          <li>name: {{ currentCountry.name }}</li>
        </ul>
      </div>
      <svg />
    </div>
  </div>
</template>
<script>
import {json, equirectangular} from 'd3';
import {max} from 'd3-array';
import {scaleLinear} from 'd3-scale';
import {select, selectAll, event, mouse} from 'd3-selection';
import {geoPath, geoEqualEarth} from 'd3-geo';
import {zoom, zoomIdentity, zoomTransform} from 'd3-zoom';
import {feature} from 'topojson-client';

export default {
  name: 'WorldMap',
  data: function() {
    return {
      country: undefined,
      currentCountry: undefined,
      width: 0,
    };
  },
  computed: {
    height() {
      return this.width / 2.01803398875; // golden ratio
    },
  },
  mounted() {
    this.width = this.$el.getBoundingClientRect().width;
    this.addResizeListener();
    this.drawMap();
  },
  methods: {
    selectCountry(country) {
      this.country = country;
    },
    openInfo(country) {
      this.currentCountry = country;
    },
    closeInfo() {
      this.currentCountry = undefined;
    },
    async drawMap() {
      // Load map data
      // const geoJsonUrl = 'https://raw.githubusercontent.com/superpikar/indonesia-geojson/master/indonesia.geojson';
      const geoJsonUrl = 'https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json';
      // try {
      // const mapData = await json(geoJsonUrl);
      // const features = mapData.features;
      const mapData = await json(geoJsonUrl);
      const features = feature(mapData, mapData.objects.countries).features;

      // Set svg width & height
      let centered = undefined;
      // const mapCenter = {
      // lat: 1.4,
      // lat: 0.0,
      // lng: 0.0,
      // lng: 117.5,
      // };
      const size = {
        height: this.height,
        width: select('.map-wrapper').node().getBoundingClientRect().width,
      };

      const color = scaleLinear()
          .domain([1, 20])
          .clamp(true)
          .range(['#336633', '#336633']);

      // const projection = geoEquirectangular()
      const projection = geoEqualEarth()
      // .scale(200)
          .scale((this.width/640) * 100)
          .center([0.0, 0.0])
          .translate([this.width / 2, this.height / 2]);

      const path = geoPath()
          .projection(projection);

      const svg = select('svg')
          .attr('width', this.width)
          .attr('height', this.height);

      // zooming
      const zoomed = () => {
        const {transform} = event;
        g.attr('transform', transform);
        g.attr('stroke-width', 1 / transform.k);
      };

      const zoomFn = zoom()
          .scaleExtent([1, 8])
          .on('zoom', zoomed);

      const reset = (d) => {
        this.closeInfo();
        centered = null;
        // Highlight the clicked country
        mapLayer.selectAll('path').style('fill', fillFn);

        svg.transition().duration(750).call(
            zoomFn.transform,
            zoomIdentity,
            zoomTransform(svg.node()).invert([size.width / 2, size.height / 2]),
        );
      };

      const clickedNew = (d) => {
        const [[x0, y0], [x1, y1]] = path.bounds(d);
        event.stopPropagation();

        // Compute centroid of the selected path
        if (d && centered !== d) {
          const centroid = path.centroid(d);
          x = centroid[0];
          y = centroid[1];
          k = 4;
          centered = d;
          this.openInfo(d.properties);
        } else {
          x = this.width / 2;
          y = this.height / 2;
          k = 1;
          centered = null;
          this.closeInfo();
        }

        // Highlight the clicked country
        mapLayer.selectAll('path')
            .style('fill', function(d) {
              return centered && d===centered ? '#D5708B' : fillFn(d);
            });

        this.openInfo(d.properties);
        svg.transition().duration(750).call(
            zoomFn.transform,
            zoomIdentity
                .translate(size.width / 2, size.height / 2)
                .scale(Math.min(8, 0.9 / Math.max((x1 - x0) / size.width, (y1 -
                    y0) / size.height)))
                .translate(-(x0 + x1) / 2, -(y0 + y1) / 2),
            mouse(svg.node()),
        );
      };
      // Add background
      svg.append('rect')
          .attr('class', 'background')
          .attr('width', this.width)
          .attr('height', this.height)
          .on('click', clicked)
          .call(zoomFn);


      const g = svg.append('g');

      const effectLayer = g.append('g')
          .classed('effect-layer', true);
      const mapLayer = g.append('g')
          .classed('map-layer', true);

      // Update color scale domain based on data
      color.domain([0, max(features, nameLength)]);


      const clicked = (d) => {
        let x; let y; let k;

        // Compute centroid of the selected path
        if (d && centered !== d) {
          const centroid = path.centroid(d);
          x = centroid[0];
          y = centroid[1];
          k = 4;
          centered = d;
          this.openInfo(d.properties);
        } else {
          x = this.width / 2;
          y = this.height / 2;
          k = 1;
          centered = null;
          this.closeInfo();
        }

        // Highlight the clicked country
        mapLayer.selectAll('path')
            .style('fill', function(d) {
              return centered && d===centered ? '#D5708B' : fillFn(d);
            });

        // Zoom
        g.transition()
            .duration(750)
            .attr('transform', 'translate(' + this.width / 2 + ',' + this.height / 2 + ')scale(' + k + ')translate(' + -x + ',' + -y + ')');
      };

      const mouseover = (d, x, y) => {
        // Highlight hovered country
        select(y[x]).style('fill', '#1483ce');
        if (d) {
          this.selectCountry(d.properties);
        }
      };

      const mouseout = (d) => {
        this.selectCountry(undefined);
        // Reset country color
        mapLayer.selectAll('path')
            .style('fill', (d) => {
              return centered && d===centered ? '#D5708B' : fillFn(d);
            });
      };

      // Get country name length
      const nameLength = (d) => {
        const n = nameFn(d);
        return n ? n.length : 0;
      };

      // Get country name
      const nameFn = (d) => {
        return d && d.properties ? d.properties.name : null;
      };

      // Get country color
      const fillFn = (d) => {
        return color(nameLength(d));
      };

      // Draw each country as a path
      mapLayer.selectAll('path')
          .data(features)
          .enter()
          .append('path')
          .attr('d', path)
          .attr('vector-effect', 'non-scaling-stroke')
          .style('fill', fillFn)
          // .call(zoomFn)
          .on('mouseover', mouseover)
          .on('mouseout', mouseout)
          .on('click', clicked);
      // } catch (e) {
      // console.error({e});
      // }
    },
    addResizeListener() {
      // redraw the chart 300ms after the window has been resized
      window.addEventListener('resize', () => {
        this.$data.redrawToggle = false;
        setTimeout(() => {
          this.$data.redrawToggle = true;
          this.$data.width = this.$el.getBoundingClientRect().width;
          this.drawMap();
        }, 300);
      });
    },
  },
};
// fill: #99cc66;
</script>

<style>
#world-map {
  height: 100%;
  width: 100%;
}
.country-title {
  position: absolute;
  top: 50px;
  left: 150px;
  color: white;
}
.country-info {
  background: white;
  position: absolute;
  top: 150px;
  right: 20px;
  height: 400px;
  width: 300px;
}
.background {
  fill: #021019;
  pointer-events: all;
}
.map-layer {
  fill: #08304b;
  stroke: #021019;
  stroke-width: 1px;
}
</style>
