import Vue from 'vue';
import Vuetify from 'vuetify/lib';
import LRU from 'lru-cache';

Vue.use(Vuetify);

const themeCache = new LRU({
  max: 10,
  maxAge: 1000 * 60 * 60, // 1 hour
});

export default new Vuetify({
  theme: {
    options: {
      themeCache,
      minifyTheme: function(css) {
        return process.env.NODE_ENV === 'production' ?
          css.replace(/[\r\n|\r|\n]/g, '') :
          css;
      },
    },
    themes: {
      light: {
        primary: '#8bc34a',
        secondary: '#cddc39',
        accent: '#03a9f4',
        error: '#f44336',
        warning: '#607d8b',
        info: '#009688',
        success: '#4caf50',
      },
      dark: {
        primary: '#8bc34a',
        secondary: '#cddc39',
        accent: '#03a9f4',
        error: '#f44336',
        warning: '#607d8b',
        info: '#009688',
        success: '#4caf50',
      },
    },
  },
});
